import React from 'react'
import { useHistory, useLocation } from "react-router-dom";

import { makeStyles, useTheme } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import DraftsIcon from '@material-ui/icons/Drafts';
import ScheduleIcon from '@material-ui/icons/Schedule';
import PhonelinkSetupIcon from '@material-ui/icons/PhonelinkSetup';
import LanguageIcon from '@material-ui/icons/Language';
import EventAvailableIcon from '@material-ui/icons/EventAvailable';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import SendIcon from '@material-ui/icons/Send';
import AccessibilityNewIcon from '@material-ui/icons/AccessibilityNew';
import StarIcon from '@material-ui/icons/Star';
import SentimentVerySatisfiedIcon from '@material-ui/icons/SentimentVerySatisfied';
import ReplyIcon from '@material-ui/icons/Reply';

import Routes from "../constants/Routes";

const useStyles = makeStyles({
  drawerListIcon: {
    minWidth: 32,
  },
  drawerListIconSelected: {
    minWidth: 32,
    color: '#fff',
  },
  drawerListItemSelected: {
    backgroundColor: theme => theme.palette.primary.light,
  },
  toolbar: theme => theme.mixins.toolbar,
});

function MenuDrawerContent(){
  const theme = useTheme();
  const classes = useStyles(theme);
  const location = useLocation();
  const history = useHistory();

  const buildListItem = (route, icon, label) => (
    <ListItem button key={`label-${label}`}
              selected={selectedMenu(route)}
              id={`route${route.replace(/\//g, "-")}`}
              onClick={() => menuClick(route)}
              classes={{selected: classes.drawerListItemSelected}}>
      <ListItemIcon className={selectedMenu(route) ? classes.drawerListIconSelected : classes.drawerListIcon}>
        {icon}
      </ListItemIcon>
      <ListItemText primary={label} />
    </ListItem>
  );

  const menuClick = (route) => {
    history.push(route);
  };

  const selectedMenu = (route) => {
    return location.pathname === route;
  };

  return (
      <List>
        {buildListItem(Routes.invites, <DraftsIcon />, 'Invites')}
        {buildListItem(Routes.newInvite, <SendIcon />, 'Send new invite')}
        {buildListItem(Routes.friends, <AccessibilityNewIcon />, 'Friend list')}
        {buildListItem(Routes.weeklySchedule, <ScheduleIcon />, 'Weekly schedule')}
        {buildListItem(Routes.blockedDates, <EventAvailableIcon />, 'Available dates')}
        {buildListItem(Routes.setTimezone, <LanguageIcon />, 'Time zone')}
        {buildListItem(Routes.connectCalendar, <PhonelinkSetupIcon />, 'Calendar connection')}
        {buildListItem(Routes.tipsForInviting, <StarIcon />, 'Tips for inviting a friend')}
        {buildListItem(Routes.tipsForResponding, <ReplyIcon />, 'Tips for replying to an invite')}
        {buildListItem(Routes.tipsForSuccess, <SentimentVerySatisfiedIcon />, 'Tips for a great encounter')}
        {buildListItem(Routes.logout, <ExitToAppIcon />, 'Logout')}
      </List>
  )
}

export default MenuDrawerContent;
